import useGalleryStyles from './Gallery.style'
import {ReactChild, FC, useEffect} from 'react'
import {Box, Grid, ImageList, ImageListItem, useMediaQuery, useTheme} from '@mui/material'
import Page from "@/components/Page/Page";
import TextHeader from "@/components/TextHeader/TextHeader";
import UnderLineHeader from "@/components/UnderLineHeader/UnderLineHeader";
import images from '@/img/Gallery/index'

const { log } = console

interface GalleryProps {
   
   children?: ReactChild,
   
}
//const GalleryItem: any[] = images

const Gallery: FC<GalleryProps> = ({ children }) => {

    const Styles = useGalleryStyles()
    const theme = useTheme()
    const isMobileVersion = useMediaQuery(theme.breakpoints.down('sm'));

    function importAll(r: any) {
        return r.keys().map(r);
    }

    // @ts-ignore
    const images = importAll(require.context('@/img/Gallery', false, /\.(png|jpe?g|svg|jfif)$/));

    return(
        <Page title={'Галерея'}>
            <Grid
                container
                component={Styles}
                className="Gallery"
                alignItems={'center'}
                spacing={1}
                flexDirection={"column"}

            >
            <>
                <UnderLineHeader>Галерея</UnderLineHeader>

                <ImageList

                    cols={isMobileVersion ? 1 : 3}
                    gap={8}
                    variant={"masonry"}
                    //rowHeight={512}
                >

                        {images.map((item: any, index: number) => (
                            <ImageListItem
                                key={index}
                            >
                                <img src={item} alt="Loading Failed"/>
                            </ImageListItem>
                        ))}

                </ImageList>

            </>
            </Grid>
        </Page>
    )
}
export default Gallery