
import {ReactChild, FC, ReactNode} from 'react'
import { Box, styled } from '@mui/material'

const { log } = console

interface TextHeaderProps {
   
    children: ReactChild | ReactNode,
    color?: 'string',
   
}



const TextHeader: FC<TextHeaderProps> = ({ children, color }) => {

    const S_TextHeader = styled(Box)(({theme}) => ({
        color: color || theme.palette.text.disabled,
        "fontFamily": "Inter",
        "fontSize": "72px",
        "fontWeight": "700",
        "lineHeight": "87px",
        "letterSpacing": "-0.02em",
        "textAlign": "left",
        position: 'relative',
        overflow: 'visible',
        zIndex: 2,
    }))

    return(

       <S_TextHeader component={'h3'} className="TextHeader">
           {children}
       </S_TextHeader>
    )
}
export default TextHeader